import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule, Routes } from '@angular/router';
import { CashflowComponent } from './cashflow.component';
import { NgZorroAntModule } from 'src/app/ng-zorro-ant.module';
import { IonicModule } from '@ionic/angular';
import { TranslateModule } from '@ngx-translate/core';
const routes: Routes = [
  {
    path: '',
    component: CashflowComponent
  },
  {
    path: 'cashbook',
    loadChildren: () => import('./cashbook/cashbook.module').then(m => m.CashbookModule)
  },
  {
    path: 'receipts',
    loadChildren: () => import('./receipts/receipts.module').then(m => m.ReceiptsModule)
  },
  {
    path: 'expenses',
    loadChildren: () => import('./expenses/expenses.module').then(m => m.ExpensesModule)
  },
  {
    path: 'wallet',
    loadChildren: () => import('./wallet/wallet.module').then(m => m.WalletModule)
  },
  {
    path: 'diary',
    loadChildren: () => import('./diary/diary.module').then(m => m.DiaryModule)
  },
  {
    path: 'customer-debt',
    loadChildren: () => import('./customer-debt/customer-debt.module').then(m => m.CustomerDebtModule)
  },
  {
    path: 'supplier-liability',
    loadChildren: () => import('./supplier-liability/supplier-liability.module').then(m => m.SupplierLiabilityModule)
  },
  {
    path: 'beginning-customer-debt',
    loadChildren: () => import('./beginning-customer-debt/beginning-customer-debt.module').then(m => m.BeginningCustomerDebtModule)
  },
  {
    path: 'beginning-supplier-liability',
    loadChildren: () => import('./beginning-supplier-liability/beginning-supplier-liability.module').then(m => m.BeginningSupplierLiabilityModule)
  },
  {
    path: 'transfers',
    loadChildren: () => import('./transfers/transfers.module').then(m => m.TransfersModule)
  },
  {
    path: 'receiving',
    loadChildren: () => import('./receiving-money/receiving-money.module').then(m => m.ReceivingMoneyModule)
  },
  {
    path: 'money-control',
    loadChildren: () => import('./money-control/money-control.module').then(m => m.MoneyControlModule)
  }
]

@NgModule({
  declarations: [CashflowComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    IonicModule,
    TranslateModule
  ]
})
export class CashflowModule { }
