import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { VhAuth } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { LanguageService } from 'src/app/sales/services/language.service';

@Component({
  selector: 'app-cashflow',
  templateUrl: './cashflow.component.html',
  styleUrls: ['./cashflow.component.scss']
})
export class CashflowComponent implements OnInit {

  constructor(
    private router: Router,
    private vhAuth: VhAuth,
    public languageService: LanguageService,
    public vhComponent: VhComponent
  ) { }

  ngOnInit(): void {
  }
  gotoCashbook() {
    if (this.vhAuth.checkMyPermission("cashflow_view_cashbook")) this.router.navigate(['/sales/dashboard/manage/cashflow/cashbook']);
    else this.vhComponent.showToast(1500, this.languageService.translate('You do not have this rights'), "alert-toast")

  }
  gotoReceipts() {
    if (this.vhAuth.checkMyPermission("cashflow_manage_receipt")) this.router.navigate(['/sales/dashboard/manage/cashflow/receipts']);
    else this.vhComponent.showToast(1500, this.languageService.translate('You do not have this rights'), "alert-toast")

  }
  gotoExpenses() {
    if (this.vhAuth.checkMyPermission("cashflow_manage_expenses")) this.router.navigate(['/sales/dashboard/manage/cashflow/expenses']);
    else this.vhComponent.showToast(1500, this.languageService.translate('You do not have this rights'), "alert-toast")
  }
  gotoWallet() {
    if (this.vhAuth.checkMyPermission("management_enable_payment")) this.router.navigate(['/sales/dashboard/manage/cashflow/wallet']);
    else this.vhComponent.showToast(1500, this.languageService.translate('You do not have this rights'), "alert-toast")
  }
  gotoDiary() {
    if (this.vhAuth.checkMyPermission("cashflow_enable_diary")) this.router.navigate(['/sales/dashboard/manage/cashflow/diary']);
    else this.vhComponent.showToast(1500, this.languageService.translate('You do not have this rights'), "alert-toast")
  }
  gotoSupplierLiability() {
    if (this.vhAuth.checkMyPermission("cashflow_enable_debt_supplier")) this.router.navigate(['/sales/dashboard/manage/cashflow/supplier-liability']);
    else this.vhComponent.showToast(1500, this.languageService.translate('You do not have this rights'), "alert-toast")
  }
  gotoBeginningSupplierLiability() {
    if (this.vhAuth.checkMyPermission("cashflow_enable_debt_supplier_begin")) this.router.navigate(['/sales/dashboard/manage/cashflow/beginning-supplier-liability']);
    else this.vhComponent.showToast(1500, this.languageService.translate('You do not have this rights'), "alert-toast")
  }
  gotoCustomerDebt() {
    if (this.vhAuth.checkMyPermission("cashflow_enable_debt_customer")) this.router.navigate(['/sales/dashboard/manage/cashflow/customer-debt']);
    else this.vhComponent.showToast(1500, this.languageService.translate('You do not have this rights'), "alert-toast")

  }
  gotoBeginningCustomerDebt() {
    if (this.vhAuth.checkMyPermission("cashflow_enable_debt_customer_begin")) this.router.navigate(['/sales/dashboard/manage/cashflow/beginning-customer-debt']);
    else this.vhComponent.showToast(1500, this.languageService.translate('You do not have this rights'), "alert-toast")
  }
  gotoReceiving() {
    if (this.vhAuth.checkMyPermission("branch_management_receive_money")) this.router.navigate(['/sales/dashboard/manage/cashflow/receiving']);
    else this.vhComponent.showToast(1500, this.languageService.translate('You do not have this rights'), "alert-toast")
  }
  gotoTransfers() {
    if (this.vhAuth.checkMyPermission("branch_management_transfers")) this.router.navigate(['/sales/dashboard/manage/cashflow/transfers']);
    else this.vhComponent.showToast(1500, this.languageService.translate('You do not have this rights'), "alert-toast")
  }
  /**Chuyển tới trang đối soát thu chi */
  goToMoneyControl() {
    if (this.vhAuth.checkMyPermission("management_money_control")) this.router.navigate(['/sales/dashboard/manage/cashflow/money-control']);
    else this.vhComponent.showToast(1500, this.languageService.translate('You do not have this rights'), "alert-toast")
  }
}
